
.cardsArray{
    min-height: 100vh;
    width: 100vw;
    display:flex;
    gap: 2vw;
}
.textCard{
    width:45%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    gap: calc(var(--index)*3);
    margin: calc(var(--index)*10) auto;
}
.cardAbout{
text-align: center;
    display:flex;
    flex-direction:column;
    justify-content:center;
    width:calc(var(--index)*27);
    font-size:calc(var(--index)*1.4);
    font-weight:900;
    color: antiquewhite;
    text-shadow: 3px 3px 3px rgb(204, 158, 158);
}
.imgMobile{
    display: none;
}
.cardAbout p{
    font-size:calc(var(--index));
    font-weight:500;
    text-shadow: 3px 3px 3px rgb(117, 112, 112);
}

.imagesContainer{
    width: 50%;
}
.imagesWrapper{
    height: 100vh;
    display: flex;
    flex-direction: column;
    /* align-items: center; */
    justify-content:center;
    margin: 0 auto;
}
.imageScroll{    
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    } 
    .imageScroll img{
    height:calc(var(--index)*20);
    width:calc(var(--index)*27);
    border-radius: 12px;
    filter: drop-shadow(0 0 7px grey);
}

@media screen and (max-width:1280px) {
    .textCard{
        width: 100%;
        align-items: center;
        justify-content: center;
    }
    .imgMobile{
        display: block;
    }
    .imagesContainer{
        display: none;
    }
    
}