section{
    height: 100vh;
    width: 100%;
   position: relative;
   top: 0vh;
   left: 0;
   overflow: hidden;
   z-index: 0;
}
    section:after{
        content:"";
        height: 30vh;
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        background: linear-gradient(180deg ,rgba(0,0,0,0) 20%, rgba(0,0,0,1) 100%);
    }
section img{
    position: absolute;
    display: block;
    top: 0;
    left: 0;
    object-fit: cover;
}
.sky{
    width: 120vw;
    height:120vh;
    left: 0;
    top: 0;
    transform: scale(1.5);
}
.buildings{
    width:100%;
    height: 100%;
    opacity: 0;
    transform: scale(2) translateY(20vh);
}

.header{
    z-index: 20;
    position:sticky;
    top: 0;
    left: 0;
}
.header_wrapper{
width: 100%;
}
.header_wrapper img{
    padding: 0 5vw;
}
.textTitleWrapper{
    /* position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%); */
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color: white;
    font-weight: 700;
    /* width: calc(var(--index)*45); */
   width: 80%;
    /* text-align: start; */
    margin: 15vh auto;
}
.titleText{
    margin-bottom: 5vh;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-wrap: wrap;
    font-size: calc(var(--index)*6);
}
.textSubtitleWrapper{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}
.textSubtitle{
    color: white;
    font-weight: 400;
    font-size: calc(var(--index));
    width: 45%;
}
.btnHeader{
    background: transparent;
    color: white;
    border: 1px solid white;
    border-radius: 12px;
    padding: .5vw;
    cursor: pointer;
}

@media(max-width: 1280px) {
    .textTitleWrapper{
        display: flex;
        flex-direction: column;
        overflow: wrap;
      }
      .titleText{
        font-size: calc(var(--index)*3);
        justify-content: center;
        flex-wrap: wrap;
        flex-direction: column;
      }
      .textSubtitleWrapper{
        font-size: calc(var(--index));
        flex-direction: column;
        gap: 5vh;
        text-align: center;
      }
}